import React, { useMemo, useEffect, useState } from "react";
import { useLocation } from "@reach/router";
import { QRCodeCanvas } from "qrcode.react";
import "../../../../../componets/log-in/login.css";
import { events } from "../../../../../content/events";


const i18n = events.test;

const TicketViewPage: React.FC = () => {
  const location = useLocation();
  const [language, setLanguage] = useState<"EN" | "VN">("EN");

  // Extract ticket ID from URL
  const ticketId = useMemo(() => {
    const parts = (location?.pathname || "")
      .replace(/\/+$/, "")
      .split("/");
    return parts[parts.length - 1] || undefined;
  }, [location?.pathname]);

  const t = i18n[language];

  // Build QR URL
  const qrURL = ticketId
    ? `https://tinhhoaviet.org.vn/event/aid/admissions/${ticketId}/`
    : null;

  return (
    <div className="auth-shell">
      {/* LEFT SIDE */}
      <aside className="brand-side">
        <div className="brand-sheen" />
        <div className="brand-pattern" />

        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>

          <ul className="brand-features">
            {t.features.map((f: string, i: number) => (
              <li key={i} className="feature">
                <span className="tick">‚úì</span>
                <span>{f}</span>
              </li>
            ))}
          </ul>
        </div>
      </aside>

      {/* RIGHT SIDE */}
      <main className="form-side">
        <header className="form-top">
          <div className="lang-switch">
            <button
              className={`lang ${language === "EN" ? "active" : ""}`}
              onClick={() => setLanguage("EN")}
            >
              {t.langEN}
            </button>
            <span className="mid">|</span>
            <button
              className={`lang ${language === "VN" ? "active" : ""}`}
              onClick={() => setLanguage("VN")}
            >
              {t.langVN}
            </button>
          </div>
        </header>

        <section className="form-wrap" style={{ color: "var(--off-white)" }}>
          {/* TICKET HEADER */}
          <div className="intro">
            <h2 className="title" style={{ color: "var(--off-white)" }}>
              {language === "EN" ? "Your Ticket" : "V√© Tham Gia"}
            </h2>
            <p className="subtitle" style={{ color: "var(--light-gray)" }}>
              {language === "EN"
                ? "Scan or present this QR code at check-in."
                : "Vui l√≤ng xu·∫•t tr√¨nh m√£ QR khi check-in."}
            </p>
          </div>

          <div className="auth-form" style={{ textAlign: "center" }}>
            {/* IF NO TICKET ID */}
            {!ticketId && (
              <p style={{ color: "var(--gold-2)" }}>
                {language === "EN"
                  ? "Ticket ID missing from URL."
                  : "Kh√¥ng t√¨m th·∫•y Ticket ID trong ƒë∆∞·ªùng d·∫´n."}
              </p>
            )}

            {/* IF VALID TICKET ID */}
            {ticketId && (
              <>
                <div className="field">
                  <h3
                    style={{
                      color: "var(--gold-2)",
                      fontSize: 22,
                      marginBottom: 6,
                    }}
                  >
                    {t.eventName}
                  </h3>

                  <p style={{ color: "var(--off-white)" }}>{t.date}</p>
                  <p style={{ color: "var(--off-white)" }}>{t.location}</p>

                  <p
                    style={{
                      fontWeight: 700,
                      marginTop: 12,
                      color: "var(--light-gray)",
                      fontSize: "1rem",
                    }}
                  >
                    Ticket ID: <strong>{ticketId}</strong>
                  </p>
                </div>

                {/* QR CODE */}
                <div style={{ marginTop: 28 }}>
                  <QRCodeCanvas
                    value={qrURL!}
                    size={230}
                    includeMargin={true}
                  />
                  <p
                    style={{
                      marginTop: 12,
                      color: "var(--gold-2)",
                    }}
                  >
                    {language === "EN"
                      ? "Scan to verify admission."
                      : "Qu√©t m√£ ƒë·ªÉ x√°c th·ª±c v√©."}
                  </p>
                </div>
              </>
            )}
          </div>
        </section>
      </main>
    </div>
  );
};

export default TicketViewPage;